import request from "@/nxTemp/request";
import store from '@/nxTemp/store';
import config from '@/nxTemp/config/index.config.js';

//newrequest
let $http = new request({
	//
	baseUrl: config.baseUrl,
	//
	fileUrl: config.baseUrl,
	// url
	defaultUploadUrl: "api/common/v1/upload_image",
	//content-type
	header: {
		'Content-Type': 'application/json;charset=UTF-8',
		// 'project_token': config.projectToken, //token
	}
});
// token
$http.getQnToken = function(callback) {
	//
	$http.get("api/common/v1/qn_upload").then(data => {
		/*
		 *
		 *visitPrefix:
		 *token:token
		 *folderPath:
		 *region:  SCN
		 */
		callback({
			visitPrefix: data.visitPrefix,
			token: data.token,
			folderPath: data.folderPath
		});
	});
}
//
$http.requestStart = function(options) {
	console.log("", options);
	if (options.load) {
		//
		store.commit("setLoadingShow", true);
	}
	// 
	if (options.method == "FILE" && options.maxSize) {
		// : options.maxSize 
		let maxSize = options.maxSize;
		for (let item of options.files) {
			if (item.size > maxSize) {
				setTimeout(() => {
					uni.showToast({
						title: "",
						icon: "none"
					});
				}, 500);
				return false;
			}
		}
	}
	if (options.url) {
		//token
		let url = options.url.substring(options.url.lastIndexOf('/') + 1);
		if (url != 'login') {
			let token = uni.getStorageSync('token');
			if (!token) {
				store.dispatch('reLogin', '');
			} else {
				options.header['token'] = uni.getStorageSync('token');
			}
		}
	}


	return options;
}
//
$http.requestEnd = function(options) {
	//
	if (options.load) {
		// 
		store.commit("setLoadingShow", false);
	}
}
