// 
$http.requestError = function(e) {
	// e.statusCode === 0 
	if (e.statusCode === 0) {
		throw e;
	} else {
		console.log(e);
		uni.showToast({
			title: "",
			icon: "none"
		});
	}
}
export default $http;
